.whyisimm_container {
    width: 100%;
    height: 90vh;
    display: flex;
}
.whyisimm_img {
    flex-basis: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.whyisimm_img img {
    max-width: 60%;
    max-height: 80%;
    border-radius: 10%;
}
.whyisimm_description {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 50%;
    padding: 1rem;
}
.whyisimm_description h2 {
    color: #061e6e;
}
.whyisimm_description span {
    font-weight: bold;
}
.whyisimm_description .span_description {
    color: #061e6e;
    width: 0.8rem;
    margin-right: 0.4rem;
}